.contactBlock {
    min-height: 100vh;
    padding: 10px;
    display: flex;

}
.contactTitle{
    font-size: 30px;
    font-weight: 700;
    color: #333D51;
    border-bottom: 1px dashed hsla(0,0%,100%,.15);
}
.contactSpan{
    color:#333D51;
}
.contactContainerForm{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-width: 100%;
}

.contactLeft{
    min-width: 45%;
    display: flex;
    flex-direction: column;

}
.contactRight{

    width: 45%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.contactList{
    width: 100%;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-right: 10px;
    background-color: #222;
    border-radius: 10px;
    margin-top: 22px;
    margin-bottom: 22px;
    position: relative;


}
.contactListSpanOne{

}
.contactListSpanOne::before{
    content: "";
    position: absolute;
    left: 20px;
    width: 24px;
    height: 24px;
    background-image: url(../style/icon/location.png);
}
.contactListSpanTwo{

}
.contactListSpanTwo::before{
    content: "";
    position: absolute;
    left: 20px;
    width: 24px;
    height: 24px;
    background-image: url(../style/icon/phone.png);
}
.contactListSpanThree{

}
.contactListSpanThree::before{
    content: "";
    position: absolute;
    left: 20px;
    width: 24px;
    height: 24px;
    background-image: url(../style/icon/email.png);
}
.contactListSpanFour {

}
.contactListSpanFour::before{
    content: "";
    position: absolute;
    left: 20px;
    width: 24px;
    height: 24px;
    background-image: url(../style/icon/telega.png);
}

.contactListSpan{
    text-decoration: none;
    color: #fff;
    text-align: left;
    margin-left: 60px;
}
.contactNameEmail{
    display: flex;
    justify-content: space-between;

}
.contactInput {


    background-color: #222;
    border-radius: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
    min-width: 25%;
    /*height: 3vh;*/
}
.contactTextarea {
    margin-top: 30px;
    min-width: 100%;
    height: 15vh;

    background-color: #222;
    border-radius: 10px;


}
.contactButtonContainer{
    display: flex;
    justify-content: center;
}
.contactBotton {
    margin-top: 8px;
    /*min-width: 50%;*/
    text-align: center;
    text-transform: none;
    font-weight: unset;
    text-decoration: none;
    border-radius: 50px;
    padding: 15px 40px;
    color: #fff;
    display: inline-block;
    background-color: #4169e1;
    transform: scale(1);
}
.contactBotton:hover{
    /*background-color: #fff;*/
    /*color: #000;*/
    cursor: pointer;
    transform: scale(1.05);
}

input, textarea, button {
    all: unset;
}

